import logo from "../../logo.svg";
import React, { Component } from "react";
import "./App.css";
import HeaderContainer from "../../components/Header/Header";
import TaskInput from "../../components/TaskInput/TaskInput";
import Tasks from "../../components/Tasks/Tasks";
import { v4 as uuidv4 } from "uuid";
import Modal from "react-modal";


Modal.setAppElement("#root");


class App extends Component {
  state = {
    tasks: [],
    taskInput: "",
    isModalOpen: false,
    editInput: "",
    taskindex: "",
  };

  getInputTaskHandler = (event) => {
    let taskInput = event.target.value;

    this.setState({
      taskInput,
    });
  };

  addTaskHandler = (event) => {
    const tasks = [...this.state.tasks];
    if (this.state.taskInput !== "") {
      let id = uuidv4();
      let task = {
        id,
        taskTitle: this.state.taskInput,
      };
      tasks.push(task);

      this.setState({
        tasks,
        taskInput: "",
      });
    } else {
      alert("Enter a Scheduled task");
    }
  };

  editTaskHandler = (index) => {
    let tasks = [...this.state.tasks];
    let task = tasks[index];

    this.setState({
      isModalOpen: true,
      taskindex: index,
      editInput: task.taskTitle,
    });
  };
  deleteTaskHandler = (index) => {
    let tasks = [...this.state.tasks];
    tasks.splice(index, 1);

    this.setState({
      tasks,
    });
  };
  cancelEditHandler = () => {
    this.setState({
      isModalOpen: false,
    });
  };

  editInputChange = (event) => {
    let newTask = event.target.value;
    this.setState({
      editInput: newTask,
    });
  };

  updateTaskHandler = () => {
    let updatedTask = this.state.editInput;
    let taskIndex = this.state.taskindex;
    if (updatedTask !== "" && taskIndex !== "") {
      let tasks = [...this.state.tasks];
      let task = tasks[taskIndex];
      this.state.editInput = task.taskTitle;

      if (updatedTask !== null && updatedTask !== "") {
        task.taskTitle = updatedTask;
        this.setState({
          tasks,
          editInput: updatedTask,
        });
        this.setState({
          isModalOpen: false,
        });
      } 
    }
    else {
      // throw Error('input cannot be empty')
    alert("task input cannot be emptied");
    }
  };
  render() {
    let customModalStyle = {
      content: {
        backgroundColor: "#2c303a",
        top: "37%",
        left: "50%",
        right: "50%",
        bottom: "30%",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)",
        color: "white",
        textAlign: "center",
      },
    };

    return (
      <div>
        <HeaderContainer />
        <h2 className="task-title">My Tasks</h2>
        <Modal isOpen={this.state.isModalOpen} style={customModalStyle}>
          <h2>Edit Task</h2>
          <input
            type="text"
            onChange={(event) => this.editInputChange(event)}
            value={this.state.editInput}
            className="edit-input"
            required
          />
          <div className="btn-container">
            <button
              onClick={() => this.updateTaskHandler()}
              className="btn-edit"
            >
              Update
            </button>
            <button
              onClick={(event) => this.cancelEditHandler(event)}
              className="btn-cancel"
            >
              Cancel
            </button>
          </div>
        </Modal>
        <TaskInput
          getTaskInput={(event) => this.getInputTaskHandler(event)}
          addTask={(event) => this.addTaskHandler(event)}
          taskInput={this.state.taskInput}
        />
        <div className="tasks-container">
          <Tasks
            tasks={this.state.tasks}
            editTask={this.editTaskHandler}
            deleteTask={this.deleteTaskHandler}
          />
        </div>
      </div>
    );
  }
}

export default App;
