import React from 'react';
import classes from './task.module.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCoffee } from '@fortawesome/free-solid-svg-icons'
import { faTrash } from '@fortawesome/free-solid-svg-icons';
import { faEdit } from '@fortawesome/free-solid-svg-icons';

const task  =  (props) => {
    return (
         <div className={classes['task-container']} >
             <p className={classes['task-title']}>{props.taskTitle}</p>
             <div className={classes['task-container-btn']}>
             <button onClick={props.editTask} className={classes['task-btn']}><FontAwesomeIcon icon={faEdit} /></button>
             <button  onClick={props.deleteTask} className={classes['task-btn']}><FontAwesomeIcon icon={faTrash} /></button>
             </div>
         </div>
    )
}

export default task;